<template>
    <div class="jiaMeng" style="padding-bottom:80px;">
        <div>
          <img src="../../assets/img/index_43.jpg" width="100%">
        </div>
        <div class="aboutUs">
          <div :class="[showAboutUs? 'aboutUsDesc' : 'aboutUsDescPart aboutUsDesc']">
            <p>
           腾讯，1998年11月诞生于中国深圳，是一家以互联网为基础的科技与文化公司。我们的使命是 “通过互联网服务提升人类生活品质”。
        腾讯秉承着 “一切以用户价值为依归” 的经营理念，致力于为亿万网民提供优质的互联网综合服务。
            </p>
            <p>
             不断倾听和满足用户需求，引导并超越用户需求，赢得用户尊敬，
             通过提升品牌形象，使员工具有高度企业荣誉感，赢得员工尊敬，
             推动互联网行业的健康发展，与合作伙伴共成长，赢得行业尊敬，
             使产品和服务像水和电融入人们的生活，为人们带来便捷和愉悦
            </p>
            <div @click="openPDF()" style="text-align:center;padding-bottom:15px;color:#FF6600;">&gt;&gt;下载加盟方案</div>
          </div>
          <img v-if="showAboutUs==false" class="aboutUsDescBottom" @click="showAboutUs=true" src="../../assets/img/down.png">
        </div>

        <van-cell-group>
          <van-cell title="加盟申请" icon="idcard"/>
        </van-cell-group>

        <van-field v-model="userName" label="真实姓名" placeholder="请输入姓名" />
        <van-field v-model="userMobile"  label="手机号码" placeholder="请输入手机" />
        <van-field v-model="userCity" label="所在城市" placeholder="请输入城市" />
        <van-field v-model="msg2" type="textarea" placeholder="资源优势" rows="4" />
        <van-button :disabled="btnSt" :loading="btnLoading"type="warning" size="large"  @click="submit()">发送加盟申请</van-button>
    </div>
</template>

<script>
  export default {
      components: {
      },
      data() {
          return {
            btnLoading:false,
            btnSt:false,
            showAboutUs:false,
            userName: '',
            userMobile: '',
            userCity:'',
            msg1:'',            
            msg2:''
          }
      },
      methods: {
        openPDF(){
          var url="http://img.jiarixiaodui.com/company/jiameng.pdf";
          location.href=url;
        },
        submit(){
          if(!this.userName){
            this.$toast('请填写姓名');
            return false;
          }
          if(!this.userMobile){
            this.$toast('请填写手机号');
            return false;
          }else{
            var phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;
            if (!phoneReg.test(this.userMobile)) {
              this.$toast('手机号错误');
              return false;
            }
          }
          if(!this.userCity){
            this.$toast('请填写城市');
            return false;
          }
          if(!this.msg1){
            this.$toast('请填写从业简历');
            return false;
          }
          if(!this.msg2){
            this.$toast('请填写资源优势');
            return false;
          }
          var obj={
            name:this.userName,
            mobile:this.userMobile,
            city:this.userCity,
            type:'加盟',
            message1:this.msg1,
            message2:this.msg2,
          };
          console.log(obj);
          this.btnLoading=true;
          this.btnSt=false;
          this.axios.post('company/contacts',obj).then(response=>{
            var res=response.data;
            this.$toast(res.message);
            this.btnLoading=false;
            if(res.code==200){
              this.btnSt=true;
            }           
          });
        }
      },
  }
</script>

<style scoped="scoped">
  .aboutUs{
    background: #fff;
    margin:10px 0;
    padding:0 10px;
  }
  .aboutUsDescBottom{
    width:24px;
    margin:0 45%;
  }
  .aboutUsDesc{
    padding: 1px 0;
    font-size: 14px;
    color: #666666;
  }
  .aboutUsDesc p{
    text-indent: 2em;
  }
  .aboutUsDescPart{    
    max-height: 90px;
    overflow: hidden;
  }

</style>
