<template>
  <!--底部tabbar-->
    <div class="vanTabbarS">
      <van-tabbar v-model="active" >
        <van-tabbar-item  :icon="item.icon" v-for="(item,index) in menuList" :key="index" @click.native="to(item.path)" >
          {{item.name}}
        </van-tabbar-item>
      </van-tabbar>
    </div>
</template>

<script>
    export default {
        props:['act'],
        components: {
        },
        data() {
            return {
              active: 0,
              menuList:[
                {
                  name:'首页',
                  icon:'friends-o',
                  path:'/',
               },
               {
                 name:'加盟',
                  icon:'certificate',
                  path:'/jiaMeng/index',
                }
                
              ]

            }
        },
        methods: {
          to(path) {
              this.$router.push(`${path}`);
          },
        },
        created:function(){
          this.active=parseInt(this.act);
        },
        activated:function(){
          this.active=parseInt(this.act);
        },
    }
</script>

